.buy__token__modal {
    background: #a7a5a53d;

    .modal-dialog {
        max-width: 600px;
    }
}

.successModal {
    .success__body {
        // @include displayCenter(center, center);
        // flex-direction: column;
        text-align: center;

        &_column {
            width: 100% !important;

        }

        .info {
            font-size: 14px;
            margin: 30px 0 20px 0;
        }

        h4 {
            font-weight: 500;
            margin-bottom: 0;
        }

        h6 {
            font-size: 13px;
            @include displayCenter(center, center);
            flex-wrap: wrap;

            .big {
                font-size: 18px;
                margin: 0 5px;
            }
        }

        .user__id {
            background-color: #1d1d1f;
            position: relative;
            border-radius: 5px;

            p {
                cursor: pointer;
                user-select: none;
                padding: 10px;
                margin-top: 10px;

                svg {
                    margin-left: 10px;
                    font-size: 18px;
                }
            }

            &:hover {
                .toolt {
                    visibility: visible;
                }
            }

            @include mQ(390px) {
                p {
                    font-size: 10px;
                }
            }
        }

        @include mQ($sm) {
            .loader {
                width: 60px;
            }

            h3 {
                font-size: 14px;
            }

            h4 {
                font-size: 14px;
            }

            h6 {
                font-size: 13px !important;

                .big {
                    font-size: 13px !important;
                }
            }
        }
    }
}

.wallets {
    .modal-dialog {
        max-width: 430px;
    }

    .buy__cpt__header__tile {
        margin-bottom: 50px;
        margin-bottom: 50px;

        h4 {
            font-size: 20px;
        }

        @include mQ($sm) {
            margin-bottom: 30px;
        }
    }

    .buy__cpt__header__close {
        top: 17px;
    }

    .wallet {
        border-radius: 5px;
        // box-shadow: var(--box-shadow-six);
        padding: 20px;
        margin: 20px 0;
        cursor: pointer;
        transition: $transition;
        @include displayCenter(center, space-between);
        // box-shadow: $box-shadow-four;
        // @include neonEffectFour(2px);
        border: 2px solid var(--primary_theme);
        // border: 2px solid linear-gradient(#181E41, #09B0A0);

        img {
            max-width: 30px;
            max-height: 30px;
            //   margin-right: 15px;
        }

        h5 {
            margin: 0;
            font-size: 16px;
            color: var(--white_pure) !important;
        }

        &:hover {
            // box-shadow: var(--box_shadow_seven);
            background: #0f3b7bad;
        }

        @include mQ($sm) {
            padding: 10px;
            margin: 15px 0;

            img {
                max-width: 20px;
                max-height: 20px;
            }

            h5 {
                margin: 0;
                font-size: 14px;
            }
        }
    }
}

.metaMaskNotFound {
    @include mQ($sm) {
        h4 {
            font-size: 20px !important;
        }
    }

    .metaMask__body {
        @include displayCenter(center, center);
        flex-direction: column;

        img {
            width: 100px;
            margin-top: 50px;
        }

        h5 {
            margin-top: 20px;
            font-size: 20px;
        }

        svg {
            font-size: 50px;
            color: var(--white_pure)
        }

        .download {
            border: 1px solid rgb(196, 196, 196);
            border-radius: 33px;
            padding: 8px 60px;
            transition: $transition;
            color: #000;

            &:hover {
                box-shadow: var(--box_shadow_one);
                transform: translateY(-2px);
            }
        }

        @include mQ($sm) {
            h5 {
                font-size: 16px;
            }
        }
    }
}

.Disconnecct {
    .modal-dialog {
        max-width: 480px;
    }

    .user__id {
        p {
            //   @include userAddress(390px);
            text-align: center;
        }

        @include mQ(450px) {
            p {
                @include userAddress(300px);
                text-align: center;
            }
        }

        @include mQ(350px) {
            p {
                @include userAddress(200px);
                text-align: center;
            }
        }
    }

    .wallet_icon {
        width: 50px;
        height: 50px;
        padding: 10px;
        border-radius: 50%;
        margin: auto auto 20px auto;
        box-shadow: var(--box_shadow_seven);

        img {
            @include image(contain);
        }

        @include mQ($sm) {
            width: 30px;
            height: 30px;
            padding: 5px;
            margin: auto auto 10px auto;
        }
    }

    @include mQ($sm) {
        button {
            font-size: 12px;
        }
    }
}

.modal_fail {
    .modal-content {
        @include neonEffectModal(0.3rem, var(--neon_background_two));
    }
}

.modal_success {
    .modal-content {
        @include neonEffectModal(0.3rem, var(--neon_background_seven));
    }
}

.modal_loading {
    .modal-content {
        @include neonEffectModal(0.3rem, var(--neon_background_eight));
    }
}

.attributes {
    .lol {
        position: relative;
    }

    .trash {
        position: absolute;
        top: 35%;
        left: 19px;
        cursor: pointer;
        transform: translateY(50%);
        width: 30px;
        height: 30px;
        background: var(--primary_theme);
        border-radius: 50%;
        @include displayCenter(center, center);
    }
}

.greenpopup {
    color: #00d6b3 !important;
}

.redpopup {
    color: #ff8282 !important;
}

.newChange {
    justify-content: center !important;
    width: 50%;
}